import React, { PropTypes } from 'react';
import styled from 'styled-components';
import { colors } from '../styledComponents';

const Container = styled.div`
  flex: 1;
  text-align: center;
`;

const Number = styled.span`
  padding: 10px 25px;
  color: ${colors.navy};
  margin: 20px;
  text-align: center;
  font-size: 40px;
  font-weight: 300;
  border-radius: 80px;
  border: 1px solid #333;

  &:hover {
    border-width: 2px;
    font-weight: bold;
  }
`;

const Body = styled.div`
  margin-top: 20px;
`;

const NumerotedBlock = ({ number, children }) => (
  <Container>
    <Number>{number}</Number>
    <Body>{children}</Body>
  </Container>
);

NumerotedBlock.propTypes = {
  number: PropTypes.number.isRequired,
  children: PropTypes.node.isRequired,
};

export default NumerotedBlock;



