import React from 'react';
import { Link } from 'react-router';
import headerPhoto from '../../assets/photo-header.jpg';
import { Container, Content, Row, TextLink, HeaderPhoto, ButtonHeaderContainer }
  from '../../components/Home/homeStyledComponents';
import { Button3 } from '../../components/styledComponents';
import HowItWorks from '../../components/Home/HowItWorks';

const Home = () => (
  <Container>
    <Content>
      <Row>
        <TextLink>comment ça marche</TextLink>
        <TextLink>soyez tilliste</TextLink>
        <TextLink>{"l'application"}</TextLink>
      </Row>
      <div style={{ position: 'relative' }}>
        <HeaderPhoto src={headerPhoto} alt="" />
        <ButtonHeaderContainer>
          <Link to={'/order/step1'}>
            <Button3>{'>> réserver <<'}</Button3>
          </Link>
        </ButtonHeaderContainer>
      </div>
      <HowItWorks />
    </Content>
  </Container>
);

export default Home;



