import styled from 'styled-components';
import { colors, mobileThresholdPixels } from '../styledComponents';

export const Row = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 10px 0px;
`;

export const TextLink = styled.a`
  color: ${colors.navy};

  &:hover {
    transform: scale(1.1);
  }
`;

export const Content = styled.div`
  width: 100%;
  background-color: white;
`;

export const Container = styled.div`
  display: flex;
  justify-content: center;
`;

const headerPhotoMargin = 40;
export const HeaderPhoto = styled.img`
  margin: 0px ${headerPhotoMargin}px;
  object-fit: cover;
  height: 800px;
  width: calc(100vw - ${2 * headerPhotoMargin}px);
`;

export const Button = styled.div`
  display: inline-block;
  zoom: 1;
  min-width: 220px;
  text-transform: uppercase;
  background: 0;
  border: 2px solid white;
  cursor: pointer;
  font-weight: bold;
  font-size: 18px;
  margin: 20px auto;
  padding: 20px 25px;
  letter-spacing: 1px;
  -webkit-transition: all .1s cubic-bezier(.7,0,.3,1);
  transition: all .1s cubic-bezier(.7,0,.3,1);
  border-color: ${props => props.isNotValid ? colors.grey : colors.white};
  background: transparent;
  color: #FFF;
  transition: all 0.3s ease;
  opacity: 0.9;
  border-radius: 4px;

  &:hover {
    opacity: 1;
    transform: scale(1.1);
  }

  @media (max-width: ${mobileThresholdPixels}) {
    padding: 20px 25px;
    font-size: 14px;
    margin-top: 15px;
  }
`;

export const ButtonHeaderContainer = styled.div`
  position: absolute;
  bottom: 100px;
  width: 100%;
  text-align: center;
`;

export const Title2 = styled.h2`
  text-align: center;
  width: 100%;
  color: ${colors.navy};
  font-size: 48px;
  font-weight: 300;
`;



